@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pinyon+Script&display=swap');


*{
  padding: 0;
  margin: 0;
}


.ff-Pd{
  font-family: 'Playfair Display', serif;
}

.ff-ss{
  font-family: sans-serif;
}

.ff-ps{
 font-family: 'Pinyon Script', cursive;;
}

.f-10{
  font-size: 10px;
}

.margin-l-2{
  margin-left: 2px !important;
}

.margin-lr-capsule{
  margin-left: 1.5px !important;
  margin-right: 2px !important;
}
.header-border{
  width: 1px;
  height: 25px;
}
/* theme  */
.light {
    color: #000 !important;
    background-color: #ffffff !important;
}

.dark{
    color: #eff2f7 !important;
    background-color: #3f425a !important;
}

.text-dark-blue{
  color: #0d43a0;
}

.bg-dark-blue{
  background-color:#31344f !important;
}
.bg-light-blue{
  background-color: #4883e9;
}

.bg-dark-light{
  background-color: #f1f1fa !important;
}

.fixed-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #4883e9; 
  z-index: 100;
}

/* profile section */
.btn-default,.btn-primary,.btn-success,.btn-info,.btn-warning,.btn-danger {
  border-radius:23px;
}

/* skill */

.skill-line{
    width: 51rem;
    height: 2px;
    border: 2px solid !important;
}

.zoom:hover {
  transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

.skill{
  background: linear-gradient(140.53deg, rgba(255, 255, 255, 0.5) 12.34%, rgba(255, 255, 255, 0) 51.46%) !important;
  width: 5rem !important;
  background-color: #4883e9 !important;

}


.project-btn{
  border: 2px solid #0d43a0;
}
.project-btn:hover{
  color: #3f425a;
}


.filter-invert{
  filter: invert(1);
}
